import React, { Component } from 'react'
import Layout from 'Containers/Layout'
import styled from '@emotion/styled'
import { Link } from 'gatsby'
import { Icon, Breadcrumb, Button } from 'antd'
import { css } from '@emotion/core'
import { Container, Typography, Row, Col } from 'reus'
import { graphql } from 'gatsby'

class SubLi extends Component {
  constructor(props) {
    super(props)
    this.state = {
      toggleMenu: false,
      toggleMainContent: false
    }
  }
  componentDidMount() {
    this.setState({toggleMenu: this.props.slug === this.props.node.fields.slug})
  }
  handleClick = e => {
    this.setState(({toggleMenu}) => ({toggleMenu: !toggleMenu}))
  }
  render() {
    const SubLi = styled.li`
      padding: 5px 0;
    `
    const MenuLink = ({children, href}) => (
      <div>
        <Link to={`${href}`} css={theme => css`
          display: flex;
          justify-content: space-between;
          padding: 10px 0;
          align-items: center;
          span {
            font-weight: bold;
            color: ${theme.color.primary};
          }
        `}>
          <span>{children}</span>
          <Icon type={this.state.toggleMenu ? 'minus' : 'plus'} css={theme => css`
            color: ${theme.color.secondary};
            margin-left: 16px;
          `}/>
        </Link>
      </div>
    )
    const SubLink = styled.a`
      color: #333;
      font-size: 14px;
    `
    return (
      <li>
        <MenuLink href={this.props.node.fields.slug} onClick={this.handleClick}>
          {this.props.node.frontmatter.title}
        </MenuLink>
        <ul css={css`
          overflow: hidden;
          transition: height .3s ease;
          height: ${this.state.toggleMenu ? this.ulEl.scrollHeight : 0}px;
          & > li {
            &:last-of-type {
              padding-bottom: 30px;
            }
          }
        `} ref={node => this.ulEl = node}>
          {
            this.props.node && this.props.node.headings.map((item, i) => (
              <SubLi key={i}>
                <SubLink href={`#`}>
                  {item.value}
                </SubLink>
              </SubLi>
            ))
          }
        </ul>
      </li>
    )  
  }
}

class HelpCenterDetail extends Component {
  constructor(props) {
    super(props)
    this.state = {
      toggleSide: false,
      toggleMainContent: false
    }
  }
  componentDidMount() {
    if (window.innerWidth <= 768) {
      this.setState({toggleSide: true})
      if (this.state.toggleSide) {
        this.setState({toggleMainContent: true})
      }
    } else {
      this.setState({toggleSide: false})
      this.setState({toggleMainContent: true})
    }
    window.addEventListener('resize', this.handleResize, false)
  }
  componentWillMount() {
    if (window !== 'undefined')
      window.removeEventListener('resize', this.handleResize, false)
  }
  handleResize = () => {
    if (window.innerWidth <= 768) {
      this.setState({toggleSide: true})
      if (this.state.toggleSide) {
        this.setState({toggleMainContent: true})
      }
    } else {
      this.setState({toggleSide: false})
      this.setState({toggleMainContent: true})
    }
  }
  render() {
    const Sidebar = styled.div`
      position: relative;
      left: 0;
      top: 0;
      margin-left: ${this.state.toggleSide ? -262 : 0}px;
      width: 250px;
      flex: none;
      padding: 20px 10px;
      transition: all .7s ease;
      ul {
        list-style: none;
      }
    `
    const ContentWrapper = styled.div`
      position: relative;
      display: flex;
      margin-bottom: 40px;
    `
    const MainContent = styled.div`
      @media screen and (max-width: ${({theme}) => theme.breakPoints.md}) {
        margin-right: ${this.state.toggleSide ? 0 : -240}px;
      }
      @media screen and (min-width: ${({theme}) => theme.breakPoints.md}) {
        margin-right: 0;
      }
      padding: 30px 90px;
      @media screen and (max-width: ${({theme}) => theme.breakPoints.sm}) {
        padding: 30px 8px;
      }
      position: relative;
      transition: all .7s ease;
      h2 {
        font-size: 24px;
        border-bottom: 3px solid #e8e8e8;
        display: inline-block;
      }
      p {
        color: #6d6d6d;
      }
    `
    const pathNames = this.props.pathContext.slug.split('/')

    const Alert = () => (
      <div css={css`
        padding: 12px 15px;
        background: #e4e4e4;
        margin-top: 60px;
      `}>
        <Row justify="space-between" align="center">
          <Col>
            <div css={css`
              display: flex;
              align-items: center;
            `}>
              <span css={css`padding-right: 20px;`}>Was this article helpful?</span>
              <div>
                <Button type="default">Yes</Button>
                <Button type="default">No</Button>
              </div>
            </div>
          </Col>
          <Col>
            <div>
              <button><Icon type="close" /></button>
            </div>
          </Col>
        </Row>
      </div>
    )
    return (
      <Layout>
        <Container xl gutter={20}>
          <div css={css`
            padding: 30px 5px 10px 5px;
            display: flex;
            align-items: center;
          `}>
            <div css={theme => css`
              padding: 5px 10px;
              margin-right: 20px;
              vertical-align: middle;
              cursor: pointer;
              border-radius: 4px;
              background: #eaeaea;
              @media screen and (min-width: ${theme.breakPoints.md}) {
                display: none;
              }
            `} onClick={() => this.setState(({toggleSide, toggleMainContent}) => ({
              toggleSide: !toggleSide,
              toggleMainContent: !toggleMainContent
            }))}>
              <Icon type="bars" />
            </div>
            <Breadcrumb separator={<Icon css={css`
              font-size: 12px;
              transform: scaleX(.7);
            `} type="right" />}>
              {
                pathNames.map((item, i) => {
                  if (item) {
                    return <Breadcrumb.Item key={i}>
                      <Link css={css`text-transform: capitalize;`} to="help-center">
                        {item.replace('-', ' ')}
                      </Link>
                    </Breadcrumb.Item>
                  }
                })
              }
            </Breadcrumb>
          </div>
          <ContentWrapper>
            <Sidebar>
              <div>
                <Typography
                  tag="h4"
                  type="tout"
                  css={theme => css`
                    color: ${theme.color.primary};
                    padding-bottom: 20px;
                  `}
                >
                  What can we help you with?
                </Typography>
              </div>
              <ul>
                {
                  this.props.pathContext.dataMenus.map((item, i) => {
                    return <SubLi key={i} node={item.node} slug={this.props.data.markdownRemark.fields.slug}/>
                  })
                }
              </ul>
            </Sidebar>
            <MainContent>
              <div>
                <div dangerouslySetInnerHTML={{__html: this.props.data.markdownRemark && this.props.data.markdownRemark.html}}></div>
                <Alert
                  message="Warning Text Warning Text Warning TextW arning Text Warning Text Warning TextWarning Text"
                  type="warning"
                  closable
                />
              </div>
            </MainContent>
          </ContentWrapper>
        </Container>
      </Layout>
    )
  }
}

export default HelpCenterDetail

export const pageQuery = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      excerpt
      timeToRead
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
  }
`